import React from 'react';
import MetaTags from 'react-meta-tags';

function About(props){

    return(
            <div className="container">
            <MetaTags>
                <title>AutoZone, Inc. | About Us</title>
                <meta name="description" content="Buy batteries, brake pads, oil, or any other parts you need online and get free shipping with next day delivery or pick up your purchase at a store near you." />
                <meta property="og:title" content="AutoZone, Inc. | About Us" />
              </MetaTags>
                <h3 className="center">About Us</h3>
                <div className="box">
                   <p>Coming Soon</p>
                </div>
            </div>
        )
}

export default About;