import React from 'react';
import BannerCarousel from './BannerCarousel';
import MetaTags from 'react-meta-tags';

import { connect } from 'react-redux'
import { addToCart } from './actions/cartActions'

function Home(props){
    
    const handleClick = (id)=>{
        props.addToCart(id); 
    }

       let itemList = props.items.map(item=>{
            return(
                <div className="card" key={item.id}>
                        <div className="card-image">
                            <img src={item.img} alt={item.title}/>
                            
                            <span to="/" className="btn-floating halfway-fab waves-effect waves-light red" onClick={()=>{handleClick(item.id)}}><i className="material-icons">add</i></span>
                        </div>

                        <div className="card-content">
                        <span className="card-title">{item.title}</span>
                            <p>{item.desc}</p>
                            <p><b>Price: {item.price}$</b></p>
                        </div>
                 </div>

            )
        })

        return(
            <div className="container">
              <MetaTags>
                <title>AutoZone Auto Parts - Buy Online or in a Store Near You</title>
                <meta name="description" content="AutoZone is the leading auto parts retailer. You'll always find the best replacement parts, aftermarket accessories for cars, trucks and SUVs online or In-store. Get yours today!" />
                <meta property="og:title" content="AutoZone Auto Parts - Buy Online or in a Store Near You" />
              </MetaTags>
              <BannerCarousel></BannerCarousel>
              <h3 className="center">Our items</h3>
              <div className="box">
                {itemList}
              </div>
            </div>
        )
}
const mapStateToProps = (state)=>{
    return {
      items: state.items
    }
  }
const mapDispatchToProps= (dispatch)=>{
    
    return{
        addToCart: (id)=>{dispatch(addToCart(id))}
    }
}

export default connect(mapStateToProps,mapDispatchToProps)(Home)